////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// layout mixins - start

@mixin eltdfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin eltdfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin eltdfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin eltdfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin eltdfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin eltdfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin eltdfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin eltdfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin eltdfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// transition mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }

    input[type=checkbox] + label span.eltdf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .eltdf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .eltdf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color;
        opacity: 0;

        @include eltdfTransition(opacity 0.3s ease-in-out);
    }

    input[type=checkbox]:checked + label .eltdf-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// common mixins - start

@mixin eltdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin eltdfImageOverlayHoverStyle($with-hover: true) {
    @if ($with-hover) {
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include eltdfAbsoluteHolderLayout();

            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;

            @include eltdfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include eltdfAbsoluteHolderLayout();

        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;

        @include eltdfTransition(opacity .2s ease-in-out);
    }
}

@mixin eltdfStandardPaginationStyle($list_type: null) {
    @include eltdfRelativeHolderLayout(top);
    font-family: $default-heading-font;
    color: #333;
    font-size: 19px;
    font-weight: 400;
    margin: 40px 0 0;
    clear: both;

    ul {
        @include eltdfRelativeHolderLayout(top);

        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 3px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                color: $default-heading-color;
                margin: 0;
                padding: 0;

                &:hover{
                    color: $first-main-color;
                }
            }

            &.eltdf-pag-active {
                a {
                    color: $first-main-color;
                }
            }

            &.eltdf-pag-prev,
            &.eltdf-pag-next,
            &.eltdf-pag-first,
            &.eltdf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 22px;
                    line-height: 22px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                &.eltdf-pag-prev {
                    a {
                        opacity: 0;
                    }
                }

                &.eltdf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color;
                }

                a {
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: "dripicons-v2" !important; // same icon pack as in our templates for pagination
                            font-size: 22px;
                            line-height: 22px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: 5px;

                        &:before {
                            content: "\55";
                        }
                    }

                    &.next {
                        margin-left: 5px;

                        &:before {
                            content: "\56";
                        }
                    }
                }
            }
        }
    }
}

@mixin eltdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 17px;
    line-height: 2em;
    font-weight: 600;
    outline: none;
    box-sizing: border-box;

    -webkit-appearance: none;
    -webkit-border-radius:0px;
    border-radius: 0;


    @include eltdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin eltdfButtonSize($size: medium) {
    @if ($size == 'small') {
        padding: 14px 33px;

    } @else if ($size == 'medium') {
        padding: 15px 43px;

    } @else if ($size == 'large') {
        padding: 16px 53px;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 17px 66px;
    }
}

@mixin eltdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin eltdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 2px solid transparent $important;
}

@mixin eltdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin eltdfButtonOutlineColor($important: null) {
    color: $default-text-color $important;
    background-color: transparent $important;
    border: 2px solid $default-text-color $important;
}

@mixin eltdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $default-text-color $important;
    border-color: $default-text-color $important;
}

@mixin eltdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

