// mixins styles - start

%input-style {
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    box-sizing: border-box;
    color: $default-heading-color;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    margin: 0 0 $input-margin;
    outline: 0;
    padding: $input-vertical-padding $input-horizontal-padding;
    position: relative;
    width: 100%;

    -webkit-appearance: none;

    @include eltdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include eltdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

